<template>
  <div class="app-container">
    <!-- 组件 -->
    <el-container>
      <el-header>
        <div class="top clearfix">
          <el-row>
            <!-- logo -->
            <el-col
              :xs="20"
              :sm="20"
              :md="5"
              :lg="6"
            >
              <div class="logo">
                <img
                  src="../assets/img/1.jpg"
                  height="60"
                >
                <h4>课程目标达成度评价系统</h4>
              </div>
            </el-col>
            <el-col
              :span="14"
              class="hidden-sm-and-down"
            >
              <ul class="menu">
                <router-link to="/index">
                  <li>
                    首页
                  </li>
                </router-link>
                <router-link to="/student">
                  <li>
                    学生
                  </li>
                </router-link>
                <router-link to="/course">
                  <li>
                    课程体系
                  </li>
                </router-link>
                <div v-if="role != 3">
                  <router-link to="/userMana">
                    <li>
                      用户管理
                    </li>
                  </router-link>
                </div>
                <router-link to="/graduationRequire">
                  <li>
                    毕业要求
                  </li>
                </router-link>
              </ul>
            </el-col>
            <el-col :span="4">
              <span class="status">
                您以
                <router-link to="/">{{ username }}</router-link>
                登录 (
                <router-link to="/login">退出</router-link>
                )
              </span>
            </el-col>
          </el-row>
        </div>
      </el-header>
      <el-row class="hidden-md-and-up">
        <el-col class="clearfix">
          <ul class="menu1">
            <router-link to="/index">
              <li>
                首页
              </li>
            </router-link>
            <router-link to="/student">
              <li>
                学生
              </li>
            </router-link>
            <router-link to="/course">
              <li>
                课程体系
              </li>
            </router-link>
            <router-link
              class="hidden"
              to="/userMana"
              v-if="role != 3"
            >
              <li>
                用户管理
              </li>
            </router-link>
            <router-link to="/graduationRequire">
              <li>
                毕业要求
              </li>
            </router-link>
          </ul>
        </el-col>
      </el-row>
      <div
        class="breadcrumbBox"
        v-show="$route.path != '/index'"
      >
        <!-- 面包屑 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">
            首页
          </el-breadcrumb-item>
          <el-breadcrumb-item>{{ path }}</el-breadcrumb-item>
          <el-breadcrumb-item v-if="path1 || path2">
            {{ path1 || path2 }}
          </el-breadcrumb-item>
        </el-breadcrumb>
        <span class="speciality">{{ speciality }}</span>
      </div>
      <el-row>
        <el-col :lg="24">
          <!-- 主体显示部分 -->
          <el-main>
            <div
              class="totop"
              ref="totopref"
              v-show="isshow"
              @click="totop"
            >
              <i class="iconfont icon-top" />
            </div>
            <router-view
              @getpath1="showpath1"
              @getpath2="showpath2"
              @getspeciality="getspeciality"
            />
          </el-main>
        </el-col>
      </el-row>
      <el-footer>
        <p>©2018 南京文软信息科技有限公司</p>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      username: JSON.parse(window.sessionStorage.user).username,
      role: JSON.parse(window.sessionStorage.user).role.role_Id,
      speciality: '',
      isshow: false,
      path: '',
      path1: null,
      path2: null
    }
  },
  created () {
    console.log(this.role)
    console.log(this.role === 1)

    this.getpath(this.$route.path)
  },
  mounted () {
    const then = this
    then.totopshow()
    window.onscroll = function () {
      then.totopshow(then)
    }
  },
  methods: {
    totopshow (then = this) {
      var scrollTop = document.body.scrollTop || document.documentElement.scrollTop
      if (scrollTop % 3 != 0) return
      if (scrollTop >= 100) {
        then.isshow = true
      } else {
        then.isshow = false
      }
    },
    totop () {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    },
    getpath (path) {
      switch (path) {
        case '/student':
          this.path = '学生'
          break
        case '/course':
          this.path = '课程管理'
          break
        case '/userMana':
          this.path = '用户管理'
          break
        case '/graduationRequire':
          this.path = '毕业要求'
          break
        default:
          break
      }
      if (path === '/course') {
        this.path1 = '查看状态'
      } else {
        this.path1 = null
      }
      if (path === '/graduationRequire') {
        this.path2 = '查看状态'
      } else {
        this.path2 = null
      }
    },
    showpath1 (val) {
      this.path1 = val
    },
    showpath2 (val) {
      this.path2 = val
    },
    getspeciality (val) {
      this.speciality = val
    }
  },
  watch: {
    '$route.path': function (newval, oldval) {
      this.getpath(newval)
    }
  }
}
</script>

<style lang="less" scoped>
a {
  text-decoration: none;
}
.el-main {
  min-width: 1190px;
}
.top {
  height: 100%;
  border-radius: 4px;
  border: 1px solid #e7e7e7;
  background-color: #f8f8f8;
}
.logo {
  display: flex;
  justify-content: start;
  img {
    margin-left: 5px;
  }
  h4 {
    margin: 0;
    margin-left: 10px;
    font-size: 18px;
    color: #666;
    line-height: 60px;
  }
}
.menu {
  list-style: none;
  display: flex;
  a {
    color: #666;
  }
  li {
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
  }
}
.menu1 {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding: 0 40px;
  li {
    height: 40px;
    line-height: 40px;
    border-radius: 4px;
    padding: 0 20px;
    color: #333;
  }
  li:hover {
    background-color: #eee;
  }
}
.status {
  display: inline-block;
  line-height: 60px;
}
.el-footer {
  height: 98px;
  margin-top: 50px;
  background-color: #555;
  p {
    color: #fff;
    margin-top: 10px;
    text-align: center;
  }
}
.totop {
  width: 35px;
  border-radius: 4px;
  text-align: center;
  position: fixed;
  right: 10px;
  bottom: 20px;
  margin-top: 20px;
  z-index: 2;
  background-color: #337ab7;
  cursor: pointer;
  > i {
    color: #fff;
  }
}
.totop:hover {
  background-color: #4d99da;
}
i {
  font-size: 24px;
  color: #999;
}
.el-button {
  margin-top: 8px;
  margin-left: 24px;
}
.breadcrumbBox {
  position: relative;
}
.speciality {
  position: absolute;
  right: 35px;
  bottom: 0;
}
.hidden {
  display: none;
}
</style>
